<template>
	<div id="orderInfo">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">订单详情</div>
        <div class="van-nav-bar__right">
          <a @click="tel(orderInfo.shop.tel)">
            <img src="static/img/icon_kf_dfhxq@2x.png" width="20px" style="vertical-align: middle;"/>
          </a>
        </div>
      </div>
    </div>
    <div class="login_box">
      <div style="margin: 5px 0;padding: 10px" class="background_color_ffffff">
        <div style="display: inline-block">
          <img src="static/img/icon_sjr_dfxxq@2x.png" width="16px" style="margin-right: 10px;"/>
        </div>
        <div class="font_12" style="display: inline-block">
          <span>{{address.username}}<span class="color_8b8b8b">&nbsp;&nbsp;&nbsp;&nbsp;{{address.telephone}}</span></span>
          <br>
          <span class="color_8b8b8b">{{address.area}}&nbsp;{{address.detailaddress}}</span>
        </div>
      </div>
      <div style="margin: 5px 0;" class="background_color_ffffff">
        <div class="background_color_ffffff" style="padding: 10px;">
          <div>
            <img :src="orderInfo.shop.shoplogo" style="vertical-align: bottom;height: 16px;"/>
            <span class="font_14">{{orderInfo.shop.shopname}}</span>
            <span class="float_right font_12 color_8b8b8b" style="margin-top: 1px">订单编号:{{orderInfo.orderid}}</span>
          </div>
          <van-card style="margin-top: 0">
            <div slot="thumb">
              <img :src="orderInfo.goods.goodslogo.split(',')[0]" v-lazy="orderInfo.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
            </div>
            <div slot="tags" class="font_12 padding_bottom_5">
              {{orderInfo.goods.goodsname}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 42px;overflow: auto;">
              {{orderInfo.goods.introduce}}
            </div>
          </van-card>
          <!--<div class="font_12" style="padding: 5px 0">-->
            <!--<span>运费</span>-->
            <!--<span class="float_right">¥5.00</span>-->
          <!--</div>-->
          <div class="font_12" style="padding: 5px 0">
            <span>付款时间</span>
            <span class="float_right">{{orderInfo.paytime}}</span>
          </div>
          <div class="font_12 text_right" style="padding: 5px 0">
            共{{orderInfo.goods.num}}件商品,合计<span class="color_ff3312">¥{{orderInfo.totalmoney}}</span>
          </div>
          <div class="text_right font_12 color_8b8b8b" style="padding: 5px 0">
            <span>订单状态:</span>
            <span v-if="orderInfo.status==0">待付款</span>
            <span v-else-if="orderInfo.status==1">已付款/待发货</span>
            <span v-else-if="orderInfo.status==2">已发货/待收货</span>
            <span v-else-if="orderInfo.status==3">待评价</span>
            <span v-else-if="orderInfo.status==4">完成</span>
            <span v-else-if="orderInfo.status==5">已退款</span>
          </div>
          <div class="text_right" style="padding: 5px 0">
            <button class="font_12 color_8b8b8b backMoney" v-if="orderInfo.status==1" @click="returnOrder">退款</button>
            <button class="font_12 color_8b8b8b backMoney" v-if="orderInfo.status==1&&orderInfo.returntype==0">退款审核中</button>
            <button class="font_12 color_8b8b8b backMoney" v-if="orderInfo.status==1&&orderInfo.returntype==1">退款成功</button>
            <button class="font_12 color_8b8b8b backMoney" v-if="orderInfo.status==1&&orderInfo.returntype==2">退款拒绝</button>
            <button class="font_12 color_40a3f3 comment" @click="toComment" v-if="orderInfo.status==3">评价</button>
          </div>
        </div>
      </div>
    </div>
    <van-actionsheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
    />
	</div>
</template>
<script>
	export default {
		name: 'orderInfo',
		data() {
			return {
        show:false,
			  address:[],
        actions:[],
        orderInfo:[],
        orderId : this.$route.query.data.orderid,
        userId:localStorage.getItem('userId'),
			}
		},
    mounted() {
      this.getOrderInfo();
      this.getAddress();
    },
		methods:{
      tel(telNum){
        dsBridge.call('dialingTel',telNum);
      },
		  submitReturn(){
        let $this = this;
        let obj = {
          // userid : this.userId,
          orderid : this.orderInfo.orderid,
          // status : 5,
        };
        let qs = require('qs');
        $this.$ajax.post("order/returnOrder", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '退款申请成功,请等待审核!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      returnOrder() {
        this.$dialog.confirm({
          title: '退款',
          message: '请确认您是否选择退款?'
        }).then(() => {
          this.submitReturn();
        }).catch(() => {
          // on cancel
        });
      },
      toComment(){
        this.$router.push({path:'comment',query:{data:this.orderInfo}});
      },
      getAddress(){
        let $this = this;
        let obj = {
          userid: this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("address/getaddressList", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            for(let i = 0;i<rpdata.data.data.length;i++){
              rpdata.data.data[i].id = rpdata.data.data[i].username;
              rpdata.data.data[i].name = rpdata.data.data[i].area + rpdata.data.data[i].detailaddress;
            }
            $this.actions = rpdata.data.data;
            $this.address = rpdata.data.data[0];
          }
        })
        .catch(function(err) {})
      },
      getOrderInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          orderid: $this.orderId
        };
        let qs = require('qs');
        $this.$ajax.post("order/getOrderinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.orderInfo = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      onSelect(item) {
        this.show = false;
        this.address = item;
      },
		}
	}
</script>
<style scoped>
  .comment {
    border: 1px solid #40a3fa;
    background: none;
    padding: 5px 15px;
    border-radius: 5px;
  }
  .backMoney {
    border: 1px solid #8b8b8b;
    background: none;
    padding: 5px 15px;
    border-radius: 5px;
  }
  .login_box{
    margin-top: 54px;
  }
</style>
<style>
  #orderInfo .van-actionsheet__item {
    height: 30px;
    line-height: 30px;
  }
  #orderInfo .van-actionsheet__name {
    font-size: 12px;
  }
  #orderInfo .van-card__thumb {
    top: 0;
    left: 0px;
    width: 90px;
    height: 75px;
    position: absolute;
  }
  #orderInfo .van-card {
    color: #333;
    height: 75px;
    font-size: 16px;
    background: #fafafa;
    position: relative;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 5px 15px 5px 100px;
  }
</style>



